.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie {
    display: flex;
    width: 45%;
    background-color: white;
    align-items: flex-start;
    margin-bottom: 70px;
    justify-content: flex-start;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25);
    
}

.movie img{
    position: relative;
    max-width: 150px;
    width: 100%;
    top: -50px;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.50);

}

.movie .movie__title,
.movie .movie__year {
    font-weight: 300;
    margin: 0;
}
 .movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;

 }

 .movie .movie__genres{
    list-style: none;
    padding: 0;
    margin: 5px 0;
    display: flex;
    flex-wrap: wrap;
 }

 .movie__genres li,
 .movie .movie__year{
    font-size: 14px;
    margin-right: 10px;
    list-style-type: none;
    
 }

 .movie .movie__column{
    width: 80%;
 }
